.groupChild,
.groupChild:hover {
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  backdrop-filter: blur(32px);
}
.groupChild {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-23xl);
  background-color: var(--color-dimgray-100);
  width: 48px;
  height: 48px;
  mix-blend-mode: lighten;
  appearance: none;
}
.groupChild:hover {
  background-color: var(--color-tomato-200);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.groupChild:active {
  background-color: var(--color-tomato-100);
}
.groupChild:checked {
  background-color: var(--color-tomato-100);
}
